header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

/* =================== CTA ================== */

.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}


/* =================== HeaderSocials ================== */

.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}


.header__socials::before {
    content: '';
    width: 1.8px;
    height: 3rem;
    background: var(--color-primary);
}

.header__socials::after{
    content: '';
    width: 1.8px;
    height: 3rem;
    background: var(--color-primary);
}


/* =================== IMAGE ================== */

.me{
    background: radial-gradient(var(--color-primary), transparent);
    width: 19rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 2rem;
    margin-left: 2rem;
    border-radius: 5rem 5rem 0 0 ;
    overflow: hidden;
    padding: 0.6rem 0.6rem 0.5;
}

/* =================== Go Down ================== */
.go__down{
    position: absolute;
    right: -2.3rem;
    bottom: 9.5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* ==================== MEDIAS (MEDIUM DEVICES)=================*/

@media screen and (max-width: 1024px){
   header{ 
        height: 68vh;
}
}
/* ==================== MEDIAS (SMALL DEVICES)=================*/

@media screen and (max-width: 600px) {

    header{
        height: 100vh;
    }
    .header__socials, .go__down{
      display: none;  
    }
}
    
